.Footer {
    background-color: #3b85f5;
    padding: 30px;
    text-align: center;
    transition: all 0.3s;
}

.Footer.dark {
    background-color: #1f4287;
}

.Footer__githubIcon {
    color: #000;
    transition: all 0.4s !important;
}
.Footer__githubIcon:hover {
    color: #deeaff;
    transition: all 0.2s !important;
}
.Footer__githubIcon.dark {
    color: #fff;

}
.Footer__githubIcon.dark:hover {
    color: #58c8eb;
}
