.header {
    display: flex;
    justify-content: center;
    padding: 20px 0;
}
.header__wrapper {
    display: flex;
    position: fixed;
    align-items: center;
    justify-content: space-between;
    width: 85%;
    margin: 0 auto;
    padding: 10px 20px;
    text-align: center;
    background-color: #3b85f5;
    border-radius: 10px;
    transition: all 0.3s;

}
.header__wrapper.dark {
    background-color: #1f4287;
}
.header__themeButton {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 32px;
    width: 32px;
    border-radius: 50%;
    transition: all 0.4s;
    color: #070042;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    cursor: pointer;
}
.header__themeButton.dark {
    color: #58c8eb;
}
.header__themeButton:hover {
    background-color: rgba(169, 192, 199, 0.3);
    transition: all 0.3s;
}
.Nav__list {
    display: flex;
}
.Nav__list a{
    color: #070042;
}
.Nav__list.dark a{
    color: #58c8eb;
}
.Nav__li {
    margin: 5px 10px;
    list-style: none;
}
.Nav__link {
    padding: 3px 8px;
    font-weight: 500;
    text-decoration: none;
    transition: all 0.4s;
}
.Nav__link:hover {
    transition: all 0.2s;
    background-color: rgba(201, 201, 201, 0.3);
    border-radius: 10px;
}
