.title {
    margin: 40px 0 0;
    padding: 0 0 0 10px;
    font-size: 30px;
    font-weight: 600;
    border-bottom: 2px solid #3b85f5;
}
.title.dark {
    border-bottom: 2px solid #1f4287;
}
.content {
    margin: 20px 0 0 30px;
}
.aboutMe__text {
    margin: 0 0 20px 0;
}

.aboutMe__btn {
    display: inline-flex;
    align-items: center;
    padding: 7px 10px 7px 15px;
    font-weight: 600;
    font-size: 18px;
    text-decoration: none;
    color: #04002b;
    background-color: #3b85f5;
    border-radius: 10px;
    transition: all 0.2s;
}
.aboutMe__btn.dark {
    color: #58c8eb;
    background-color: #1f4287;
}
.aboutMe__btn:hover {
    color: #deeaff;
    background-color: #04002b;
    transition: all 0.4s;
}
.aboutMe__btn.dark:hover {
    color: #071E3D;
    background-color: #58c8eb;
}
.skills__list {
    padding: 0;
}
.skills__li {
    list-style: none;
    display: flex;
    align-items: center;
    margin: 7px 0;
}
.skills__name {
    padding: 0 0 0 10px;
    font-size: 18px;
    font-weight: 500;
}
.education__subtitle {
    font-size: 20px;
    font-weight: 600;
}
.education__text {
    padding: 5px 10px;
}
