.Works__title {
    font-size: 28px;
    font-weight: 600;
    margin: 10px 0 20px;
}
.Works__linkBlock {
    display: inline-flex;
    flex-direction: column;
}
.Works__imgLink {
    display: inline;
    width: 100%;
}
.Works__img {
    border-radius: 15px;
}
.Works__link {
    padding: 10px 0;
    text-decoration: none;
    color: #1f4287;
}
.Works__link.dark {
    color: #c3d2f0;
}